.btn {
	padding: 15px 25px;
	font-family: $montserrat;
	font-weight: 300;
	line-height: 1.3;
	color: #e9082f;
	border-radius: 5px;
	font-size: 20px;
	vertical-align: top;
	background-color: $white;
	display: inline-block;
	&_orange {
		background-color: #e89c2a;
		color: $white; }
	&_big {
		padding: 16px 60px 14px;
		font-size: 24px;
		font-weight: 600;
		border-radius: 10px;
		text-transform: uppercase;
		line-height: 1.1; } }
