.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	padding: 0;
	z-index: 20;
	&__inner {
		margin: 0 auto;
		padding: 0 30px;
		max-width: 1806px;
		@include clr; }
	&__logo {
		float: left;
		margin-top: 30px; }
	&__btn-menu {
		float: right; }
	&__right {
		float: right;
		margin-top: 57px;
		transform: translateX(calc(100% - 68px));
		.is-loaded & {
			transition: transform .3s ease-in-out; }
		.is-open-menu & {
			transform: translateX(0); } } }

.logo {
	width: 90px;
	vertical-align: top;
	display: inline-block;
	img {
		max-width: 100%;
		display: block; } }

.btn-menu {
	position: relative;
	z-index: 1;
	margin-top: 57px;
	width: 47px;
	height: 37px;
	background-color: transparent;
	&__line {
		position: absolute;
		left: 0;
		height: 8px;
		width: 100%;
		transition: transform .3s;
		background-color: #FF9D11;
		&:first-child {
			top: 0;
			transform-origin: left top;
			.is-open-menu & {
				transform: translate(11px, -1px) rotate(45deg); } }
		&:nth-child(2) {
			top: 50%;
			margin-top: -4px;
			.is-open-menu & {
				transform: scaleX(0); } }
		&:last-child {
			bottom: 0;
			transform-origin: left bottom;
			.is-open-menu & {
				transform: translate(11px, 0) rotate(-45deg); } } } }
.search {
	margin-right: 30px;
	vertical-align: top;
	display: inline-block;
	@include clr;
	&__btn {
		float: right;
		font-size: 0;
		background-color: transparent;
		.icon {
			font-size: 37px;
			color: #ff9d11; } }
	&__wrap {
		float: right;
		padding-right: 35px;
		margin-left: -335px;
		@include clr; }
	&__input {
		float: right;
		width: 0;
		transition: width .3s;
		overflow: hidden;
		.is-open & {
			width: 300px; }
		input {
			padding: 5px 10px 5px 0;
			width: 300px;
			color: $white;
			font-size: 20px;
			font-family: $openSans;
			height: 37px;
			border-bottom: 3px solid #636161;
			background-color: transparent;
			display: block; } } }

.nav {
	opacity: 0;
	visibility: hidden;
	font-size: 0;
	vertical-align: top;
	display: inline-block;
	transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
	.is-open-menu & {
		opacity: 1;
		visibility: visible; }
	&__list {
		text-transform: uppercase;
		font-weight: 700; }
	&__item {
		margin-right: 30px;
		font-size: 25px;
		display: inline-block;
		&.is-active {
			.nav__link {
				color: #0d70b7; } } }
	&__link {
		color: $white;
		transition: color .3s;
		@include hover {
			color: #0d70b7; } } }
